/* eslint-disable */
const initPopup = () => {
  const blockHeader = document.querySelector('.region__header');
  const block = document.querySelector('.region__block');

  blockHeader.addEventListener('click', (evt) => {
    evt.stopPropagation();
    block.classList.toggle('is-active');
  });

  document.body.addEventListener('click', (evt) => {
    evt.stopPropagation();
    if (!block.contains(evt.target)) {
      block.classList.remove('is-active');
    }
  });
};

export { initPopup }
